.flex {
    // FIXME: это костыль
    display: flex;
    align-items: center;
}

html {
    scroll-behavior: smooth;
}

body {
    color: #fff;
    font-family: Montserrat;
    background-color: $color_bg;
    // overflow-y: hidden;
}

body::-webkit-scrollbar {
    width: 2px;
    background-color: rgba(#fff, 0.15);
    border-radius: 0px;
    height: 200px;
}

body::-webkit-scrollbar-thumb {
    background: #fff;
    border-radius: 0px;
}

section {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    padding: 64px 0px;

    height: 100vh !important;
    height: calc(var(--vh, 1vh) * 100);
}

a:visited {
    color: #fff !important;
}

.title {
    h3 {
        font-size: $size_h3;
        line-height: 39px;
        font-weight: 300;
    }
}

.content_wrapper {
    h4 {
        font-size: $size_h4;
        @include bebas(BebasBook);
        margin-bottom: 0px;
    }

    p {
        margin-bottom: 0px;
    }
}

.content_inner {
    font-size: $size_text;
    font-weight: 400;
    line-height: 2.8rem;
    border-left: 2px solid #fff;
    padding: 10px 16px;
    margin-bottom: 40px;

    &:last-child {
        margin-bottom: 0px;
    }

    p {
        margin-bottom: 16px;

        &:last-child {
            margin-bottom: 0px;
        }
    }
}

.fix_offset {
    margin-bottom: 40px;
}

.content_btn {
    padding: 23px 16px;
}

.btn {
    @include bebas(BebasRegular);
    font-size: $size_btn;
    text-transform: uppercase;
    color: #fff;
    line-height: 29px;
    padding: 4px;
    margin-left: 16px;
    border: none;
    outline: none;
    transition: background 0.6s, box-shadow 0s; //FIXME: Не работает плавность перхода заднего фона
    position: relative;

    &:hover::before {
        opacity: 1;
    }

    &:hover {
        cursor: pointer;
    }

    &:active {
        @include active()
    }

    &::before {
        @include hover();
        top: 2px;
        left: 5px;
    }
}

.next,
.back {
    color: #fff;
    font-family: Montserrat;
    font-weight: 300;
    font-size: $size_arrow_link;
    text-decoration: none;
    position: absolute;
    bottom: 64px;
    // left: 135px;
    padding: 6px 26px 8px;

    &:hover::before {
        opacity: 1;
    }

    &:hover {
        cursor: pointer;
    }

    &:active {
        @include active()
    }

    &::before {
        @include hover();
        top: 7px;
        left: 24px;
    }

    &::after {
        content: ' ';
        width: 16px;
        height: 16px;
        position: absolute;
        left: -7px;
        top: 7px;
        background-image: url(../img/Vector.png); // TODO: мб сделать через svg
    }
}


.back {
    @include bebas(BebasRegular);
    font-size: 16px;

    &::after {
        content: ' ';
        width: 16px;
        height: 16px;
        position: absolute;
        left: -7px;
        top: 5px;
        background-image: url(../img/Vector_back.png); // TODO: мб сделать через svg
    }
}

.about_prev,
.clients_prev,
.team_prev {
    display: none;
}
